<template>
  <div>
    <Header name="申请售后"></Header>
    <div style="background: #f7f7f7">
      <div style="height: 10px"></div>
      <div v-for="item in dataList" :key="item.id">
      <!-- <div class="orderBody" v-if="ttype === 1"> -->
        <!-- <div class="orderTop">
          <p>订单号：<span>{{item.orderSn}}</span></p>
          <p>{{item | statusFilter}}</p>
        </div>
        <div class="orderCenter" v-for="itm in item.orderItemList" :key="itm.id" v-show="!itm.isShow">
          <div class="orderimg">
            <van-image :src="itm.productPic ? itm.productPic:''"></van-image>
          </div>
          <div class="orderfont">
            <div class="ttop">
              {{itm.productName}}
            </div>
            <div class="ccenter">{{itm.promotionName}}</div>
            <div class="bbottom">
              <div class="left">￥<span>{{itm.productPrice}}</span></div>
              <div>x{{itm.productQuantity}}</div>
            </div>
          </div>
        </div> -->
        <!-- <div class="orderBottom">
          <div>退款金额</div>
          <div><span>￥{{returnMoney || 0}}</span></div>
        </div> -->
      <!-- </div> -->
      <div class="orderBody" v-if="ttype === 1 || ttype === 2">
        <div class="orderTop">
          <p>订单号：<span>{{item.orderSn}}</span></p>
          <p>{{item | statusFilter}}</p>
        </div>
        <div class="orderCenter" v-for="itm in item.orderItemList" :key="itm.id">
          <div class="orderimg">
            <van-image :src="itm.productPic ? itm.productPic:''"></van-image>
          </div>
          <div class="orderfont">
            <div class="ttop">
              {{itm.productName}}
            </div>
            <div class="shopSpecs" v-if="itm.productAttr">
              <div v-for="(ite,i) in JSON.parse(itm.productAttr || `[]`)" :key="i">{{ite.value}}</div>
            </div>
            <div class="ccenter">{{itm.promotionName}}</div>
            <div class="bbottom">
              <div class="left">￥<span>{{itm.productPrice}}</span></div>
              <div>x{{itm.productQuantity}}</div>
            </div>
          </div>
        </div>
      </div>
      <van-form v-model="formDt">
      <div class="choice">
        <p class="title">选择售后类型</p>
        <div class="select">
          <van-radio-group v-model="isOnline" disabled>
            <van-cell-group style="margin: 0" :border="false">
              <van-cell title="我要退货或退款" :icon="require('@/assets/image/tui.png')"  @click="isOnline = '1'" style="padding:10px 4.23%" label="已收到货, 需要退还收到的货物">
                <template #right-icon>
                  <van-radio name="1">
                    <template #icon="props">
                      <div @click="isOnline = '1'">
                        <img class="img-icon" :src="props.checked ? require('@/assets/image/yxz.png') : require('@/assets/image/wxz.png')" />
                      </div>
                    </template>
                  </van-radio>
                </template>
              </van-cell>
              <van-cell title="我要换货" :icon="require('@/assets/image/huan.png')" @click="loanPay" style="padding:10px 4.23%" label="已收到货, 需要更换已收到的货物">
                <template #right-icon>
                  <van-radio name="2">
                    <template #icon="props">
                      <div @click="loanPay">
                        <img class="img-icon" :src="props.checked ? require('@/assets/image/yxz.png') : require('@/assets/image/wxz.png')" />
                      </div>
                    </template>
                  </van-radio>
                </template>
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </div>
      </div>
      <div class="reason">
        <div class="number" v-if="isOnline === '2'">
          <p>请选择需要换货的商品数量</p>
          <van-stepper v-model="returnNum" :max="quantity" button-size="28" input-width="50px" disable-input />
        </div>
        <van-field
          v-model="formDt.reason"
          rows="3"
          autosize
          label="退款/退货/换货原因"
          type="textarea"
          placeholder="请输入您的退款/退货/换货原因"
        />
        <div v-if="ttype !== 2">
          <p>请上传退/换货凭证</p>
          <van-uploader v-model="imgList" multiple :max-count="5" :after-read="afterRead" @delete="deleteRead"/>
        </div>
      </div>
      <div class="submitBtn" @click="onSubmit(item.orderSn,quantity,item.createTime)">提交</div>
      </van-form>
      </div>
      <div style="height: 28px"></div>
    </div>
  </div>
</template>

<script>
import * as api from '@/data/api/market/index'
import Header from '@/components/Layout/header/index'
import axios from 'axios';
export default {
  components :{
    Header
  },
  data() {
    return {
      returnNum:1,
      isOnline: '1',
      orderItemIds: 0,
      ttype: Number(this.$route.query.type),
      otherType: Number(this.$route.query.otherType) ? Number(this.$route.query.otherType) : null,
      returnMoney: 0,
      baseUrl: process.env.BaseUrl + '/minio/upload',
      form: {
          pageNum: 1,
          pageSize: 50,
          total: 0,
          status: -1
      },
      dataList:[],
      formDt:{
        reason: '',
        orderSn: '',
        proofPics:[],
        returnType: this.$route.query.type
      },
      imgList:[],
      checked:false,
      orderStatus: 0,
      quantity: 0
    }
  },
  filters:{
    statusFilter (itm) {
        // 0->待付款；1->待发货；2->已发货；3->已完成；4->已关闭；5->无效订单
        if (itm.status === 0) {
          if (itm.payType === 3 && itm.payVoucher) {
            return '待审核'
          } else {
            return '待付款'
          }
        } else if (itm.status === 1) {
          return '待发货'
        } else if (itm.status === 2) {
          return '待收货'
        } else if (itm.status === 3) {
          return '已完成'
        } else if (itm.status === 4) {
          return '已取消'
        } else if (itm.status === 5) {
          return '无效订单'
        }
      },
  },
  mounted() {
    this.shopdetails();
  },
  methods: {
    loanPay(){
      if(this.orderStatus === 1){
        this.$notify({
          message: '该商品不符合换货要求！',
          color: '#ad0000',
          background: '#ffe1e1',
        })
        return
      }
      this.isOnline = '2'
      this.checked = false
    },
    shopdetails() {
      let me = this;
      api.getOrderList(me.form).then((res) => {
          if (res.code === 200) {
            if (res.data.list) {
              res.data.list.forEach((item) => {
                if(item.id === Number(me.$route.query.id)){
                  me.dataList = [item];
                  me.orderStatus = item.status
                  item.orderItemList = item.orderItemList.filter(i => {
                    return i.id === Number(me.$route.query.Fid)
                  })
                  console.log(item);
                  me.returnMoney = item.orderItemList[0].productPrice * item.orderItemList[0].productQuantity
                  me.quantity = item.orderItemList[0].productQuantity
                  me.orderItemIds = item.orderItemList[0].id
                }
              });
            } else {
              me.dataList = [];
            }
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally(() => {});
    },
    onSubmit(id,quantity,time){
      if (this.formDt.reason) {
        let overTime = +new Date(time) + (24*60*60*1000*7)
        let lastTime = +new Date(time) + (24*60*60*1000*15)
        let nowTime = +new Date()
        if(this.orderItemIds !== 0){
          this.formDt = {
            reason: this.formDt.reason,
            orderSn: id,
            proofPics: this.formDt.proofPics.join ? this.formDt.proofPics.join(',') : this.formDt.proofPics,
            orderItemIds: this.orderItemIds
          }
        }else{
          this.formDt = {
            reason: this.formDt.reason,
            orderSn: id,
            proofPics: this.formDt.proofPics.join ? this.formDt.proofPics.join(',') : this.formDt.proofPics,
          }
        }
        if(this.isOnline === '1'){
          if(this.ttype === 1){
            // 申请退货
            if(nowTime >= overTime){
              this.$notify({
                message: '7天之后无法申请退货!',
                color: '#ad0000',
                background: '#ffe1e1',
              })
              return
            }
            api.returnApplyCreate(this.formDt).then(res => {
              if (res.code === 200) {
                this.$router.push({
                  path:'/user/myorders',
                  query: {
                    'toorders':'-1',
                  }
                })
                this.$notify({
                  message: '申请成功',
                  type: 'success',
                })
              } else {
                this.$notify({
                  message: res.message,
                  color: '#ad0000',
                  background: '#ffe1e1',
                })
              }
            }).catch((res) => {
              this.$notify({
                message: res.message,
                color: '#ad0000',
                background: '#ffe1e1',
              })
            }).finally(() => {
            })
          }else {
            if(this.otherType) {
              this.formDt.orderItemIds = Number(this.$route.query.Fid)
            }
            api.returnApplyRefund(this.formDt).then(res => {
              if (res.code === 200) {
                this.$router.push({
                  path:'/user/myorders',
                  query: {
                    'toorders':'-1',
                  }
                })
                this.$notify({
                  message: '申请成功',
                  type: 'success',
                })
              } else {
                this.$notify({
                  message: res.message,
                  color: '#ad0000',
                  background: '#ffe1e1',
                })
              }
            }).catch((res) => {
              this.$notify({
                message: res.message,
                color: '#ad0000',
                background: '#ffe1e1',
              })
            }).finally(() => {
            })
          }
        }else{
          //换货
          if(nowTime >= lastTime){
            this.$notify({
              message: '15天之后无法申请换货!',
              color: '#ad0000',
              background: '#ffe1e1',
            })
            return
          }
          let newForm = {
            orderSn: this.formDt.orderSn,
            orderItemId: this.formDt.orderItemIds,
            quantity: this.returnNum,
            reason:this.formDt.reason,
            proofPics: this.formDt.proofPics,
          }
          api.returnApplyExchange(newForm).then(res => {
            if (res.code === 200) {
              this.$router.push({
                path:'/user/myorders',
                query: {
                  'toorders':'-1',
                }
              })
              this.$notify({
                message: '申请成功',
                type: 'success',
              })
            } else {
              this.$notify({
                message: res.message,
                color: '#ad0000',
                background: '#ffe1e1',
              })
            }
          }).catch((res) => {
            this.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }).finally(() => {
          })
        }
      } else {
        this.$notify({
          message: '退款/退货/换货原因不能为空',
          color: '#ad0000',
          background: '#ffe1e1',
        })
      }
    },
    deleteRead (file) {
      let me = this
      me.formDt.proofPics.forEach((item,index)=>{
        if(item.indexOf(file.file.name) > 0){
          me.formDt.proofPics.splice(index,1)
        }
      })
    },
    afterRead (file) {
      let me = this
      file.status = 'uploading';
      file.message = '上传中...';
      var formData = new FormData();
      formData.append('file', file.file);
      const instance=axios.create({ withCredentials: true})
      let config = { headers: { "Content-Type": "multipart/form-data" }};
      instance.post(me.baseUrl, formData, config).then(res => {
        if (res.data.code === 200 ) {
          file.status = '';
          file.message = '';
          me.formDt.proofPics.push(res.data.data.url)
        } else {
          file.status = 'failed';
          file.message = '上传失败';
          me.$notify({
            message: res.data.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }
      }).catch((res) => {
        me.$notify({
          message: res.message,
          color: '#ad0000',
          background: '#ffe1e1',
        })
      }).finally(() => {
      })
    }
  },
};
</script>

<style lang="stylus" scoped>
>>>.van-field__label{
  width:10em
}
.orderBody {
  width: 94.6%; // 355
  margin: 0px 2.6% 10px;
  padding-bottom: 20px;
  background: #fff;
  border-radius: 12px;

  .orderTop {
    width: 91.6%;
    height: 50px;
    margin: 0px 4.2%;
    border-bottom: 1px solid #f3f3f3;
    display: flex;
    justify-content: space-between;
    align-items: center;

    P {
      font-size: 14px;
      color: #666666;

      span {
        color: #000;
        font-weight: bold;
      }
    }
  }

  .orderCenter {
    width: 91.6%; // 325
    height: 86px;
    margin: 20px 4.2%;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #f0f0f0;
    padding-bottom: 20px;

    .orderimg {
      width: 26.5%;
      height: 86px;
      text-align center
      >>>.van-image {
      width: 86px;
      height: 86px;
      }
    }

    .orderfont {
      width: 69.2%;
      height: 90px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      overflow hidden

      .ttop {
        width: 100%;
        height 40px
        font-size: 15px;
        font-weight: bold;
        color: #000000;
        line-height: 21px;
        overflow hidden
      }

      .shopSpecs {
          display: flex;
          flex-wrap: wrap;
          font-size: 13px;
          margin-bottom: 5px;
          font-weight: 400;
          color: #888888;

          div{
            margin-right: 10px;
            margin-bottom: 5px;
          }
        }

      .ccenter {
        width: fit-content;
        padding: 0 3px;
        text-align: center;
        line-height: 18px;
        background: #FEE7D1;
        border-radius: 3px;
        font-size: 10px;
        color: #F48A1B;
      }

      .bbottom {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .left {
          font-size: 15px;
          color: #FD2744;

          span {
            font-size: 18px;
            font-weight: bold;
          }
        }

        div {
          font-size: 12px;
          color: #666666;
        }
      }
    }
  }

  .orderBottom {
    width: 91.6%;
    margin: 20px 4.2% 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    div {
      font-size: 13px;
      color: #333333;

      span {
        font-size: 15px;
        font-weight: bold;
        color: #111111;
      }
    }
  }
}

.choice{
  width: 94.6%;
  height: 173px;
  margin: 10px 2.6%;
  background: #FFFFFF;
  border-radius: 12px;
  overflow: hidden;
  .title{
    margin: 16px 0px 6px 16px
    font-size: 16px;
    font-weight: bold;
    color: #000000;
  }
  .select{
    .van-cell__left-icon{
      margin-right: 12px;
      font-size: 20px;
      line-height: 44px;
    }
    .img-icon{
      width: 22px;
      height: 22px;
    }
  }
}

.reason{
  width: 94.6%; // 355
  margin: 10px 2.6%;
  background: #FFFFFF;
  border-radius: 12px;
  overflow: hidden;
  .number{
    margin 15px 15px 0px 15px;
    padding-bottom:15px;
    border-bottom: 1px solid #ebedf0;
    p{
      margin: 0px 0px 15px;
      color:#646566;
    }
  }
  >>>.van-cell {
    flex-direction: column;
  }
  >>>.van-uploader__upload {
    margin 0px 0px;
  }
  >>>.van-uploader__preview{
    margin 0px 4px 8px 0px;
  }
  >>>.van-uploader{
    margin 10px 0px 10px 15px;
  }
  p{
    margin 15px 0px 0px 15px;
    color:#646566;
  }
}

.submitBtn {
  width: 88%;
  height: 48px;
  background: #FF4C4C;
  border-radius: 4px;
  margin: 28px 5.8% 0px;
  text-align: center;
  line-height: 48px;
  color: #fff;
}
</style>


